.room-selector {
  display: inline-block;
  font: 400 14px sans-serif;
  position: relative;
  width: 20em;
}
.room-selector-wrap {
  position: relative;
  border-bottom: solid 1px lightgray;
}
.room-selector-title {
  background: #ffffff;
  border: 1px solid transparent;
  box-sizing: border-box;
  cursor: pointer;
  height: 2em;
  padding: 0 1.5em 0 .5em;
  position: relative;
  text-align: left;
  width: 100%;
  font-size: 14px;
  font-family: 'arial';
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.room-selector-title:after {
  border-color: #a5a5a5 transparent transparent transparent;
  border-style: solid;
  border-width: 8px 5px;
  content: '';
  display: block;
  height: 0;
  position: absolute;
  right: .5em;
  top: 40%;
  width: 0;
}
.room-selector-list {
  background: #fff;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: none;
  height: 0;
  list-style: none;
  margin: 0;
  opacity: 0;
  padding: 0;
  position: absolute;
  transition: .2s linear;
  width: 100%;
  z-index: 999;
}
.room-selector-opened {
  display: block;
  height: auto;
  opacity: 1;
}
.room-selector-list > li {
  cursor: pointer;
  padding: .5em .5em;
  text-align: left;
  font-size: 14px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.room-selector-list > li:hover {
  background-color: #fff5e9;
}
.room-selector-list > .room-selector-selected {
  background-color: #ffdfb6;
}



