/* supress selection for non-input elements */
:not(input) {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
  cursor: default;
}
body > .panel {
  min-width: 250px;
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg)
  }
}

.viewport {
  width:                        100%;
  height:                       100%;
  overflow:                     hidden;
  -overflow-y:                  auto;
  border-top:                   solid 3px red;
}
.viewport.debug {
  box-sizing:                   border-box;
  border:                       dotted 1px lightGray;
  position:                     absolute;
  width:                        auto;
  height:                       auto;
  top:                          1em;
  left:                         1em;
  bottom:                       1em;
  right:                        1em;
  perspective:                  2000px;
}

.panel {
  display:                      flex;
  display:                      -webkit-flex;
  /*width:                        100%;*/
  height:                       100%;
  flex-direction:               column;
  font-family:                  sans;
  font-size:                    10pt;
}

.action {
  cursor:                       pointer;
}
.action .disabled {
  cursor:                       default;
}

.button {
  display: block;
  /* min-width: 115px; */
  padding: 0.5em 1em;
  margin: 1em;
  border-radius: 1.25em;
  font-size: 14px;
  text-align: center;
  /* color: white; */
  cursor: pointer;
  text-decoration: none;
  background-color: #3C5064;
  color: #E7E7E7;
  box-sizing: border-box;
}

.button.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.input-password, .input-config, .select-config  {
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0.5em;
  font-size: inherit;
}

.panel.empty {
  flex-grow:                    0;
  flex-shrink:                  0;
}
.panel-centered {
  display:                      flex;
  display:                      -webkit-flex;
  flex-direction:               column;
  flex:                         1 1;
  text-align:                   center;
  justify-content:              center;
  align-items:                  center;
  width: 100%;
}
.panel-head {
  overflow:                     hidden;
  text-overflow:                ellipsis;
  margin:                       0;
  padding:                      16px;
  width:                        100%;
  box-sizing:                   border-box;
  font-size:                    28px;
  text-transform:               lowercase;
  letter-spacing:               8px;
  background-repeat:            no-repeat;
  background-position:          center;
  background-size:              50%;
  color:                        transparent;
}
.panel-logo {
  background-image:             url('../img/zang-spaces-icon.svg');
  background-repeat:            no-repeat;
  background-position:          center center;
  background-size:              contain;
  height:                       1em;
  width:                        1em;
  font-size:                    96px;
  border-radius:                50%;
  background-color:           white;
}
.panel-note-head {
  font-size:                    16px;
  color:                        #66BABF;
  padding:                      8px 0;
}
.panel-note, .panel-note-text {
  box-sizing:                   border-box;
  padding:                      24px 0;
  width:                        100%;
  max-width:                    300px;
  font-size:                    13px;
}

.panel-label {
  box-sizing:                   border-box;
  padding:                      0px 0px 3px 0px;
  width:                        100%;
  font-size:                    13px;
}

.panel.fixed {
  /* flex-grow:                    0; */
  /* flex-shrink:                  0; */
  /* height: auto; */
  flex-direction:               row;
  align-items: flex-end;
  /* justify-content:              center; */
  overflow:                     hidden;
}
