  outline: none;
}
select:focus.cfgwizard-select {
  outline: none;
}


.input-config-wrap > .icon-clear {
  background-size: 75%;
}
.input-empty > .icon-clear, .disabled + .icon-clear {
  visibility: hidden;
}

.input-config-wrap {
  border-bottom: solid 1px #3C5064;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 0.75em;
}

.cfgwizard-input, .cfgwizard-select {
  color: #3C5064;
  background-color: transparent;
  border: none;
  font-family: sans;
}

.cfgwizard-input.disabled {
  opacity: 0.5;
}

.cfgwizard-action {
  background-color: #F7941E;
  color: white;
}
.cfgwizard-login {
  text-align: center;
  width: 100%;
}
.cfgwizard-login .action {
  color: #007AFF;
  text-decoration: underline;
}

.error-sourceinit-icon {
  background-image: url(../img/error.svg);
}
.error-sourceretry-icon {
  background-image: url(../img/spin.svg);
  background-size: 100%;
  -webkit-animation: spin 1s steps(8) infinite;
  animation: spin 1s steps(8) infinite
}

/* scrollbars */
::-webkit-scrollbar 
{
  width:            4px;
  opacity:          0.25;
  cursor:           pointer;
}
::-webkit-scrollbar:hover
{
  opacity:          1;
}
::-webkit-scrollbar-button:start:decrement, 
::-webkit-scrollbar-button:end:increment 
{
  display:          block;
}
::-webkit-scrollbar-button:vertical:increment,
::-webkit-scrollbar-button:vertical:decrement,
::-webkit-scrollbar-button:vertical:start:increment,
::-webkit-scrollbar-button:vertical:end:decrement 
{
  display:          none;
}
::-webkit-scrollbar-track-piece 
{
  background-color: lightgray;
}
::-webkit-scrollbar-thumb:vertical 
{
  border-radius:    2px;
  background-color: rgba(64, 64, 64, .5);
}
::-webkit-scrollbar-thumb:vertical:hover
{
  background-color: rgba(64, 64, 64, .75);
}



.main-head {
  height:             40px;
  color:              white;
  flex-direction:     row;
  align-items:        center;

  background-color: black;
  flex:               0 0 64px;
  justify-content:    left;
}
.main-image {
  width:              48px;
  height:             48px;
  flex:               0 0 48px;
  border-radius:      100%;
  background-color:   darkgray;
  margin:             0 8px;
  overflow:           hidden;
  z-index:            2;
}
.main-cmd {
  width:              32px;
  flex:               0 0 32px;
  color:              white;
}
.main-cmd-hidden {
  display:            none;
}
.main-title {
  padding:            0 4px;
  justify-content:    center;

  width:              100%;
}
.main-name {
}
.main-user {
  opacity:            0.75;
}
.main-name, .main-user {
  /*width:              100%;*/
  overflow:           hidden;
  white-space:        nowrap;
  text-overflow:      ellipsis;
}

.img-wrap {
  width:              100%;
  height:             100%;
  position:           relative;
  display:            inline-flex;
  align-items:        center;
  justify-content:    center;
}
.img-wrap:before {
  position:           absolute;
  top:                0;
  left:               0;
  width:              100%;
  height:             100%;
  font-size:          24px;
  display:            inline-flex;
  align-items:        center;
  justify-content:    center;
}
.img-item {
  width:              100%;
  height:             100%;
  z-index:            1;
}
.img-error {
  opacity:            0;
}

.icon.buddy:before {
  content:          '\e8d8';
  content:          'person';
}

.icon-base {
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  /* width: 1em; */
  /* height: 1em; */
}

.icon-clear {
  background-image: url('../img/clear.svg');
  width: 1.5em;
  height: 1.5em;
  opacity:  0.5;
}
.icon-search {
  background-image: url('../img/search.svg');
  width: 2em;
  height: 1.5em;
  opacity: 0.5;
  flex: 0 0 2em;
  background-size: contain;
}

.panel-meeting-view-equinox > .panel-meeting-wrap {
  align-content: center;
  justify-content: center;
}

.panel-meeting-view-equinox > .panel-meeting-wrap > .panel-meeting-selector {
  height: auto;
  flex: 0 0 auto;
}

.panel-meeting-view-equinox .panel-info-item {
  text-align: left;
}

.room-select {
  margin-bottom: 10px;
}

.panel-error-box > .panel-centered {
  min-height: 18em;
}

.hidden {
  display:                      none; /* SDC 3.4 9 - .hidden moved below to show/hide Cancel/Retry buttons */
}



